import { SELLER_PROPERTY_FORMS } from './Constants';

export const fetchSellerPropertyFormsSuccess = data => {
  return {
    type: SELLER_PROPERTY_FORMS.fetchSellerPropertyFormsSuccess,
    payload: data,
  };
};

export const fetchSellerPropertyForms = () => {
  return {
    type: SELLER_PROPERTY_FORMS.fetchSellerPropertyForms,
  };
};

export const fetchSellerPropertyFormsError = data => {
  return {
    type: SELLER_PROPERTY_FORMS.fetchSellerPropertyFormsError,
    payload: data,
  };
};
