import { SELLER_PROPERTY_FORMS } from './Constants';

const initialState = {
  propertyForms: [],
  isLoading: false,
  error: null,
};

export const sellerPropertyFormsReducer = (
  state = initialState,
  { type, payload },
) => {
  switch (type) {
    case SELLER_PROPERTY_FORMS.fetchSellerPropertyForms:
      return {
        ...state,
        isLoading: true,
        error: null,
      };
    case SELLER_PROPERTY_FORMS.fetchSellerPropertyFormsSuccess:
      return {
        ...state,
        propertyForms: payload,
        isLoading: false,
        error: null,
      };
    case SELLER_PROPERTY_FORMS.fetchSellerPropertyFormsError:
      return {
        ...state,
        propertyForms: [],
        isLoading: false,
        error: payload,
      };
    default:
      return state;
  }
};
