import { call, put, takeLatest } from 'redux-saga/effects';
import { STEP } from './Constants';
import { getAllStepsSuccess, getAllStepsFailure } from './Actions';
import { StepAPI } from '../services/StepApi';

const stepApi = new StepAPI();

export function* getAllSteps() {
  yield takeLatest(STEP.getAllSteps, getAllStepsFromAPI);
}

function* getAllStepsFromAPI({ type, payload }) {
  try {
    const { data } = yield call(stepApi.getAllSteps);
    yield put(getAllStepsSuccess({ ...data }));
  } catch (error) {
    yield put(getAllStepsFailure(error));
  }
}
