// @flow
import React, { useState, Fragment, useEffect } from 'react';
import type { Element, AbstractComponent } from 'react';
import type { Offer as TOffer } from 'types/Offer';
import _get from 'lodash.get';
import { updateOfferStep as saveOfferStep } from 'containers/BuyerOffer/redux/Actions';
import { ViewOfferActionButtons as ActionButtons } from 'containers/BuyerOffer/NewOfferList/ViewOfferActionButtons';
import { ConfirmDialog } from 'components/ConfirmDialog';
import { OFFER_STEPS } from 'shared/Consts';
import { mappingCounterData } from 'helpers/Offer';
import { createStructuredSelector } from 'reselect';
import { connect } from 'react-redux';
import { withErrorBoundary } from 'components/ErrorBoundary';
import { OFFER_ERROR_MESSAGES } from 'shared/Messages';
import { toastr } from 'react-redux-toastr';
import CounterOfferModal from 'components/Offers/CounterOfferModal';
import { getConfirmDialogMessage } from './helpers';

type Props = {
  offer: TOffer,
  isUpdatingStep: boolean,
  saveOfferStep: Function,
  isUpdatedStep: boolean,
  isFormLoaded: boolean,
  isAcceptDisabled: boolean,
  error: Object,
  refetch: Function,
};
type PropsWithoutRedux = {};

const BuyerActions = (props: Props): Element<typeof Fragment> => {
  const {
    offer,
    isUpdatingStep,
    saveOfferStep,
    isUpdatedStep,
    error,
    refetch,
    isFormLoaded,
    isAcceptDisabled,
  } = props;
  const [openCounterOfferDialog, setOpenCounterOfferDialog] = useState(false);
  const [openConfirmDialog, setOpenConfirmDialog] = useState(false);
  const [confirmDialogProps, setConfirmDialogProps] = useState({
    children: '',
    onAgree: () => '',
  });

  useEffect(() => {
    if (isUpdatedStep) {
      refetch();
      setOpenConfirmDialog(false);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isUpdatedStep]);

  useEffect(() => {
    const { message } = error || {};
    if (message) {
      toastr.error(OFFER_ERROR_MESSAGES.saveFailure, message);
    }
  }, [error]);

  const handleAccept = offerId => {
    saveOfferStep({ offerId, newStep: OFFER_STEPS.advisorNeedToReviewOffer });
  };

  const handleDecline = offerId => {
    saveOfferStep({ offerId, newStep: OFFER_STEPS.buyerDecline });
  };

  const onAccept = (offerId, stepId) => {
    setOpenConfirmDialog(true);
    setConfirmDialogProps({
      children: getConfirmDialogMessage('accept'),
      onAgree: () => {
        setOpenConfirmDialog(false);
        handleAccept(offerId);
      },
    });
  };

  const onDecline = offerId => {
    setOpenConfirmDialog(true);
    setConfirmDialogProps({
      children: getConfirmDialogMessage('decline'),
      onAgree: () => {
        setOpenConfirmDialog(false);
        handleDecline(offerId);
      },
    });
  };

  const onCounter = offer => {
    setOpenCounterOfferDialog(true);
  };

  const onCloseCounterModal = ({ isReload } = {}) => {
    setOpenConfirmDialog(false);
    setOpenCounterOfferDialog(false);

    if (isReload) {
      refetch();
    }
  };

  const mappingOffer = mappingCounterData(offer);

  return (
    <>
      <ActionButtons
        offer={mappingOffer}
        onAccept={onAccept}
        onDecline={onDecline}
        onCounter={onCounter}
        disabled={isUpdatingStep}
        isAcceptDisabled={isAcceptDisabled}
      />
      <ConfirmDialog
        open={openConfirmDialog}
        onClose={() => setOpenConfirmDialog(false)}
        {...confirmDialogProps}
      />
      <CounterOfferModal
        offer={mappingOffer}
        open={openCounterOfferDialog}
        handleClickCloseDialog={onCloseCounterModal}
        isFormLoaded={isFormLoaded}
      />
    </>
  );
};

const structuredSelector = createStructuredSelector({
  isUpdatingStep: state => _get(state, ['buyerOffer', 'isLoading']),
  isUpdatedStep: state => _get(state, ['buyerOffer', 'isUpdatedStep']),
  error: state => _get(state, ['buyerOffer', 'error']),
  isFormLoaded: state => _get(state, ['form', 'isFormLoaded']),
});

const mapDispatchToProps = {
  saveOfferStep,
};

export default (connect(
  structuredSelector,
  mapDispatchToProps,
)(withErrorBoundary(BuyerActions)): AbstractComponent<PropsWithoutRedux>);
