import { STEP } from './Constants';

export const stepReducer = (
  state = { steps: [], isLoading: false, error: null },
  { type, payload },
) => {
  switch (type) {
    case STEP.getAllSteps:
      return {
        ...state,
        steps: [],
        isLoading: true,
        error: null,
      };
    case STEP.getAllStepsSuccess:
      return {
        ...state,
        steps: payload,
        isLoading: false,
        error: null,
      };
    case STEP.getAllStepsFailure:
      return {
        ...state,
        isLoading: false,
        error: payload,
      };
    default:
      return state;
  }
};
