import { FORM } from './Constants';

const initialState = {
  form: {},
  isLoading: false,
  error: null,
  message: null,
  urls: [],
  file: [],
  isFormSaved: false,
  isFileUploaded: false,
  isFormLoaded: false,
};

export const formReducer = (state = initialState, { type, payload }) => {
  switch (type) {
    case FORM.fetchForm:
      return {
        ...state,
        isFetchingForm: true,
        error: null,
        isFormLoaded: false,
      };
    case FORM.fetchFormSuccess:
      return {
        ...state,
        form: payload,
        isFetchingForm: false,
        error: null,
        isFormLoaded: true,
      };
    case FORM.fetchFormError:
      return {
        ...state,
        form: {},
        isFetchingForm: false,
        error: payload,
      };
    case FORM.saveFormData:
      return {
        ...state,
        isLoading: true,
        error: null,
        urls: [],
        isFormSaved: false,
        isFileUploaded: false,
      };
    case FORM.saveFormDataSuccess:
      return {
        ...state,
        isLoading: false,
        error: null,
        urls: payload.urls,
        isFormSaved: true,
        isFileUploaded: false,
      };
    case FORM.saveFormDataError:
      return {
        ...state,
        isLoading: false,
        error: payload,
        isFormSaved: false,
        isFileUploaded: false,
      };
    case FORM.getFormFiles: {
      const newFile = state.file;
      newFile.push(payload);
      return {
        ...state,
        file: newFile,
      };
    }
    case FORM.saveFormFiles:
      return {
        ...state,
        isLoading: true,
        error: null,
        isFileUploaded: false,
      };
    case FORM.saveFormFilesSuccess:
      return {
        ...state,
        isLoading: false,
        files: [],
        isFileUploaded: true,
      };
    case FORM.saveFormFilesError:
      return {
        ...state,
        isLoading: false,
        error: payload,
        urls: [],
        files: [],
        isFileUploaded: false,
      };
    default:
      return state;
  }
};
