import { call, put, takeLatest } from 'redux-saga/effects';
import { OFFER } from './Constants';
import { OfferAPI } from '../services/OfferApi';
import {
  fetchOffersSuccess,
  fetchOffersError,
  saveOfferStepSuccess,
  saveOfferStepError,
} from './Actions';
import _get from 'lodash/get';

const offerApi = new OfferAPI();

export function* getSellerOffers() {
  yield takeLatest(OFFER.fetchOffers, getOffersFromAPI);
}

function* getOffersFromAPI({ type, payload }) {
  try {
    const { data } = yield call(offerApi.fetchOffers);

    yield put(fetchOffersSuccess(data));
  } catch (e) {
    yield put(fetchOffersError(e));
  }
}

export function* saveSellerOfferStep() {
  yield takeLatest(OFFER.saveOfferStep, saveOfferStepToAPI);
}

function* saveOfferStepToAPI({ type, payload }) {
  const { id, offer } = payload;
  try {
    const { data } = yield call(offerApi.saveOfferStep, {
      id,
      offer,
    });
    yield put(saveOfferStepSuccess(data));
  } catch (error) {
    yield put(saveOfferStepError(_get(error, ['response', 'data'])));
  }
}
