import { OFFER } from './Constants';

export const fetchOffers = data => {
  return {
    type: OFFER.fetchOffers,
    payload: data,
  };
};

export const fetchOffersSuccess = data => {
  return {
    type: OFFER.fetchOffersSuccess,
    payload: data,
  };
};

export const fetchOffersError = data => {
  return {
    type: OFFER.fetchOffersError,
    payload: data,
  };
};

export const saveOfferStep = (id, offer) => {
  return {
    type: OFFER.saveOfferStep,
    payload: { id, offer },
  };
};

export const saveOfferStepSuccess = data => {
  return {
    type: OFFER.saveOfferStepSuccess,
    payload: data,
  };
};

export const saveOfferStepError = data => {
  return {
    type: OFFER.saveOfferStepError,
    payload: data,
  };
};
