import Request from 'helpers/Request';
import { RIVERDALE_BASE_URL } from 'shared/Consts';

export class OfferAPI {
  updateOfferStep = ({ newStep, offerId }) => {
    const endpoint = `${RIVERDALE_BASE_URL}/digioffer/buyeroffers/${offerId}/steps`;
    const body = { new_step: newStep };
    return Request.put(endpoint, body);
  };
  getAllOffers = () => {
    const endpoint = `${RIVERDALE_BASE_URL}/digioffer/buyeroffers`;
    return Request.get(endpoint);
  };
}
