// @flow
import React, { useState, Fragment, useEffect } from 'react';
import type { Element, AbstractComponent } from 'react';
import type { Offer as TOffer } from 'types/Offer';
import _get from 'lodash.get';
import { mappingCounterData } from 'helpers/Offer';
import { ActionButtons } from 'containers/SellerOffer/NewOfferList/ActionButtons';
import { ConfirmDialog } from 'components/ConfirmDialog';
import { OFFER_STEPS, ACCEPT_STEPS } from 'shared/Consts';
import { saveOfferStep } from 'containers/SellerOffer/redux/Actions';
import { createStructuredSelector } from 'reselect';
import { connect } from 'react-redux';
import { withErrorBoundary } from 'components/ErrorBoundary';
import { OFFER_ERROR_MESSAGES } from 'shared/Messages';
import { toastr } from 'react-redux-toastr';
import { getConfirmDialogMessage } from './helpers';

type Props = {
  offer: TOffer,
  isLoading: boolean,
  saveOfferStep: Function,
  isUpdatedStep: boolean,
  error: Object,
  refetch: Function,
};
type PropsWithoutRedux = {};

const SellerActions = (props: Props): Element<typeof Fragment> => {
  const { offer, isLoading, saveOfferStep, isUpdatedStep, error, refetch } =
    props;
  const [openConfirmDialog, setOpenConfirmDialog] = useState(false);
  const [confirmDialogProps, setConfirmDialogProps] = useState({
    children: '',
    onAgree: () => '',
  });

  useEffect(() => {
    if (isUpdatedStep) {
      refetch();
      setOpenConfirmDialog(false);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isUpdatedStep]);

  useEffect(() => {
    const { message } = error || {};
    if (message) {
      toastr.error(OFFER_ERROR_MESSAGES.saveFailure, message);
    }
  }, [error]);

  const handleAccept = (offerId, stepId) => {
    const newStepId = _get(ACCEPT_STEPS, stepId, OFFER_STEPS.paBeingCreated);
    saveOfferStep(offerId, { new_step: newStepId });
  };

  const handleApproval = (offerId, stepId) => {
    setOpenConfirmDialog(true);
    setConfirmDialogProps({
      children: getConfirmDialogMessage('accept'),
      onAgree: () => {
        setOpenConfirmDialog(
          stepId === OFFER_STEPS.sellerNeedToReReviewSuggestion,
        );
        handleAccept(offerId, stepId);
      },
    });
  };

  const handleDecline = offerId => {
    setOpenConfirmDialog(true);
    setConfirmDialogProps({
      children: getConfirmDialogMessage('decline'),
      onAgree: () => {
        setOpenConfirmDialog(false);
        saveOfferStep(offerId, {
          new_step: OFFER_STEPS.advisorNeedToReReviewSuggestion,
        });
      },
    });
  };

  const mappingOffer = mappingCounterData(offer);

  return (
    <>
      <ActionButtons
        offer={mappingOffer}
        onAccept={handleApproval}
        onDecline={handleDecline}
        disabled={isLoading}
      />
      <ConfirmDialog
        open={openConfirmDialog}
        onClose={() => setOpenConfirmDialog(false)}
        {...confirmDialogProps}
      />
    </>
  );
};

const structuredSelector = createStructuredSelector({
  isLoading: state => _get(state, ['sellerOffers', 'isLoading']),
  isUpdatedStep: state => _get(state, ['sellerOffer', 'isUpdatedStep']),
  error: state => _get(state, ['sellerOffer', 'error']),
});

const mapDispatchToProps = {
  saveOfferStep,
};

export default (connect(
  structuredSelector,
  mapDispatchToProps,
)(withErrorBoundary(SellerActions)): AbstractComponent<PropsWithoutRedux>);
