import { BUYER_OFFER } from './Constants';

export const updateOfferStep = data => {
  return {
    type: BUYER_OFFER.updateOfferStep,
    payload: data,
  };
};

export const updateOfferStepSuccess = data => {
  return {
    type: BUYER_OFFER.updateOfferStepSuccess,
    payload: data,
  };
};

export const updateOfferStepFailure = data => {
  return {
    type: BUYER_OFFER.updateOfferStepFailure,
    payload: data,
  };
};

export const getAllOffers = payload => ({
  type: BUYER_OFFER.getAllOffers,
});

export const getAllOffersSuccess = payload => ({
  type: BUYER_OFFER.getAllOffersSuccess,
  payload,
});

export const getAllOffersFailure = payload => ({
  type: BUYER_OFFER.getAllOffersFailure,
  payload,
});
