import { call, put, takeLatest } from 'redux-saga/effects';
import { PROPERTY } from './Constants';
import { getAllPropertySuccess, getAllPropertyFailure } from './Actions';
import { PropertyApi } from '../services/PropertyApi';
import _get from 'lodash/get';

const propertyApi = new PropertyApi();

export function* getAllProperties() {
  yield takeLatest(PROPERTY.getAllProperty, getAllPropertyFromAPI);
}

function* getAllPropertyFromAPI({ type }) {
  try {
    const { data } = yield call(propertyApi.getAllProperty);
    yield put(getAllPropertySuccess(data));
  } catch (error) {
    yield put(getAllPropertyFailure(_get(error, ['response', 'data'])));
  }
}
