import { call, put, takeLatest, takeEvery, all } from 'redux-saga/effects';
import { FORM } from './Constants';
import { FormsAPI } from '../services/FormApi';
import {
  fetchFormError,
  fetchFormSuccess,
  saveFormDataSuccess,
  saveFormDataError,
  saveFormFilesSuccess,
  saveFormFilesError,
} from './Actions';
const formApi = new FormsAPI();

export function* getForm() {
  yield takeLatest(FORM.fetchForm, getFormsFromAPI);
}

function* getFormsFromAPI({ type, payload }) {
  try {
    const { data } = yield call(formApi.fetchForms, {
      endpoint: payload,
    });
    yield put(fetchFormSuccess({ ...data }));
  } catch (e) {
    yield put(fetchFormError(e));
  }
}

export function* saveFormData() {
  yield takeEvery(FORM.saveFormData, saveFormDataToAPI);
}

function* saveFormDataToAPI({ type, payload }) {
  const { endpoint, survey } = payload;
  try {
    const { data } = yield call(formApi.saveFormData, {
      endpoint,
      survey,
    });
    yield put(saveFormDataSuccess({ urls: data.urls }));
  } catch (e) {
    yield put(saveFormDataError(e));
  }
}

export function* saveFormFiles() {
  yield takeLatest(FORM.saveFormFiles, saveFormFilesToAPI);
}

function* saveFormFilesToAPI({ type, payload }) {
  try {
    const listCalls = payload.map(({ url, file, options }) => {
      return call(formApi.saveFormFiles, {
        url,
        file,
        options,
      });
    });

    yield all(listCalls);

    yield put(saveFormFilesSuccess());
  } catch (e) {
    yield put(saveFormFilesError(e));
  }
}
