import Request from '../../../helpers/Request';

export class FormsAPI {
  fetchForms = ({ endpoint }) => {
    return Request.get(endpoint);
  };
  saveFormData = async ({ endpoint, survey }) => {
    const body = {
      FormData: {
        ...survey.data,
      },
    };
    return Request.post(endpoint, body);
  };
  saveFormFiles = ({ url, file, options }) => {
    if (url) {
      return Request.put(url, file, {
        headers: {
          ...options.headers,
          /*saving file to API does not allow AWS Authorization while the others require
          This line of code is a signal for the axios.intercepter not to add Authorization to the request*/
          Authorization: '',
        },
      });
    }
    return false;
  };
}
