import { createStore, combineReducers, applyMiddleware, compose } from 'redux';
import { reducer as toastrReducer } from 'react-redux-toastr';
import { formReducer } from './components/FormBuilder/redux/Reducer';
import { stepReducer } from 'containers/Step/redux/Reducer';
import { sellerPropertyFormsReducer } from './components/SellerProperty/Listing/Form/redux/Reducer';
import { propertiesReducer } from 'containers/Property/redux/Reducer';
import createSagaMiddleware from 'redux-saga';
import rootSaga from './sagas';
import {
  sellerOffersReducer,
  sellerOfferReducer,
} from 'containers/SellerOffer/redux/Reducer';
import { buyerOfferReducer } from 'containers/BuyerOffer/redux/Reducer';
import { calendarReducer } from 'components/Calendar/redux/Reducer';

const reducers = {
  buyerOffer: buyerOfferReducer,
  form: formReducer,
  step: stepReducer,
  sellerPropertyForms: sellerPropertyFormsReducer,
  toastr: toastrReducer, // <- Mounted at toastr.
  sellerOffer: sellerOfferReducer,
  sellerOffers: sellerOffersReducer,
  properties: propertiesReducer,
  calendar: calendarReducer,
};
const sagaMiddleware = createSagaMiddleware();

const composeEnhancers =
  typeof window === 'object' && window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__
    ? window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__({})
    : compose;

const enhancer = composeEnhancers(applyMiddleware(sagaMiddleware));

const reducer = combineReducers(reducers);
const store = createStore(reducer, enhancer);
sagaMiddleware.run(rootSaga);

export default store;
