import { call, put, takeLatest } from 'redux-saga/effects';
import { SELLER_PROPERTY_FORMS } from './Constants';
import { SellerPropertyApi } from '../services/SellerPropertyApi';
import {
  fetchSellerPropertyFormsError,
  fetchSellerPropertyFormsSuccess,
} from './Actions';

const sellerPropertyApi = new SellerPropertyApi();

export function* getForms() {
  yield takeLatest(
    SELLER_PROPERTY_FORMS.fetchSellerPropertyForms,
    getFormsFromAPI,
  );
}

function* getFormsFromAPI() {
  try {
    const { data } = yield call(sellerPropertyApi.fetchForms);
    // call the success action with data
    yield put(fetchSellerPropertyFormsSuccess(data));
  } catch (e) {
    yield put(fetchSellerPropertyFormsError(e));
  }
}
