import { call, put, takeLatest } from 'redux-saga/effects';
import { BUYER_OFFER } from './Constants';
import {
  updateOfferStepSuccess,
  updateOfferStepFailure,
  getAllOffersSuccess,
  getAllOffersFailure,
} from './Actions';
import { OfferAPI } from 'containers/BuyerOffer/services/OfferApi';
import _get from 'lodash/get';

const offerApi = new OfferAPI();

export function* getAllOffers() {
  yield takeLatest(BUYER_OFFER.getAllOffers, getAllOffersFromAPI);
}

function* getAllOffersFromAPI({ type }) {
  try {
    const { data } = yield call(offerApi.getAllOffers);
    yield put(getAllOffersSuccess(data));
  } catch (error) {
    yield put(getAllOffersFailure(_get(error, ['response', 'data'])));
  }
}

export function* updateOfferStep() {
  yield takeLatest(BUYER_OFFER.updateOfferStep, updateOfferStepToAPI);
}

function* updateOfferStepToAPI({ type, payload }) {
  try {
    const { data } = yield call(offerApi.updateOfferStep, payload);
    yield put(updateOfferStepSuccess({ ...data }));
  } catch (error) {
    yield put(updateOfferStepFailure(_get(error, ['response', 'data'])));
  }
}
