import { BUYER_OFFER } from './Constants';

export const buyerOfferReducer = (
  state = { offers: [], isLoading: false, error: null, isUpdatedStep: false },
  { type, payload },
) => {
  switch (type) {
    case BUYER_OFFER.getAllOffers:
      return {
        ...state,
        offers: [],
        isLoading: true,
        error: null,
      };
    case BUYER_OFFER.getAllOffersSuccess:
      return {
        ...state,
        offers: payload,
        isLoading: false,
        error: null,
      };
    case BUYER_OFFER.getAllOffersFailure:
      return {
        ...state,
        isLoading: false,
        error: payload,
      };
    case BUYER_OFFER.updateOfferStep:
      return {
        ...state,
        isLoading: true,
        error: null,
        isUpdatedStep: false,
      };
    case BUYER_OFFER.updateOfferStepSuccess:
      return {
        ...state,
        isLoading: false,
        error: null,
        isUpdatedStep: true,
      };
    case BUYER_OFFER.updateOfferStepFailure:
      return {
        ...state,
        isLoading: false,
        error: payload,
        isUpdatedStep: false,
      };
    default:
      return state;
  }
};
