import Request from 'helpers/Request';
import { RIVERDALE_BASE_URL } from 'shared/Consts';

export class OfferAPI {
  fetchOffers = () => {
    return Request.get(`${RIVERDALE_BASE_URL}/digioffer/selleroffers`);
  };
  saveOfferStep = async ({ id, offer }) => {
    return Request.put(
      `${RIVERDALE_BASE_URL}/digioffer/selleroffers/${id}/steps`,
      offer,
    );
  };
}
